@import url(https://fonts.googleapis.com/css?family=Merriweather|Open+Sans&display=swap);
.title {
  font-family: "Open Sans", sans-serif;
  font-size: 72px;
  font-weight: 400;
  padding-top: 100px; }

.item-title {
  font-family: "Open Sans", sans-serif;
  font-size: 26px;
  font-weight: 400;
  padding: 24px 0 36px 0;
  line-height: 30px; }

.sub-title {
  font-family: "Merriweather", serif;
  font-size: 22px;
  font-weight: 400;
  padding-bottom: 100px; }

p {
  line-height: 20px;
  margin-right: 12px; }

ul {
  position: relative;
  font-size: 14px;
  white-space: nowrap;
  margin-top: 24px; }

li {
  text-indent: 18px;
  line-height: 24px; }

li::before {
  width: 6px;
  height: 10px;
  content: '';
  position: absolute;
  -webkit-transform: translate(-18px, 7px);
          transform: translate(-18px, 7px);
  background-image: url(/marketinigWepApp/static/media/blue-chevron.7b21f96e.svg);
  background-repeat: no-repeat;
  background-position: center; }

.layout {
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr auto;
  grid-gap: 12px; }

.max-width {
  max-width: 1150px;
  margin: 0 auto;
  padding: 0 12px; }

.three-column-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 12px;
  padding-bottom: 100px; }

.three-column-grid img {
  width: 100%; }

@media (prefers-color-scheme: dark) {
  .layout .layout-header {
    background-color: #282c2f; } }

@media (prefers-color-scheme: light) {
  .layout .layout-header {
    background-color: white; } }

.header {
  display: grid;
  grid-template-columns: auto 1fr auto;
  grid-gap: 12px; }
  .header h1 {
    text-indent: -200px;
    height: 55px;
    width: 145px;
    overflow: hidden;
    background-image: url(/marketinigWepApp/static/media/logo-light.683c1c36.svg);
    background-repeat: no-repeat;
    background-position: center; }
  .header .top-nav {
    display: grid;
    grid-template-columns: auto auto auto;
    grid-gap: 12px;
    justify-items: center;
    align-items: center; }
  .header .top-nav div {
    padding-right: 24px;
    height: 55px;
    justify-items: center;
    align-items: center;
    display: grid;
    position: relative; }
  .header .top-nav div::after {
    width: 6px;
    height: 10px;
    content: '';
    right: 0;
    position: absolute;
    -webkit-transform: translate(-7px, 0px) rotate(90deg);
            transform: translate(-7px, 0px) rotate(90deg);
    background-image: url(/marketinigWepApp/static/media/blue-chevron.7b21f96e.svg);
    background-repeat: no-repeat;
    background-position: center; }

@media (prefers-color-scheme: dark) {
  .header h1 {
    background-image: url(/marketinigWepApp/static/media/logo-dark.b4a9d276.svg); } }

@media (prefers-color-scheme: light) {
  .header h1 {
    background-image: url(/marketinigWepApp/static/media/logo-light.683c1c36.svg); } }

.footer {
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr auto;
  grid-gap: 12px; }

.card {
  background-color: white;
  box-shadow: 0 1px 0px rgba(0, 0, 0, 0.03), 0 1px 1px rgba(0, 0, 0, 0.05); }

.floating-card {
  background-color: white;
  box-shadow: 0 1px 0px rgba(0, 0, 0, 0.03), 0 1px 1px rgba(0, 0, 0, 0.05);
  padding: 45px 26px 26px 26px; }

@media (prefers-color-scheme: dark) {
  .card {
    background-color: #282c2f; } }

@media (prefers-color-scheme: light) {
  .card {
    background-color: white; } }

button {
  height: 32px;
  width: 100%;
  margin-top: 24px;
  cursor: pointer;
  padding-left: 12px;
  font-weight: 600; }

.left {
  text-align: left; }

.blue {
  background-color: #3081e3;
  color: white; }

.carousel {
  height: 500px;
  display: block;
  margin: 33px 0 45px 0;
  background-image: url(/marketinigWepApp/static/media/image1.19f1c5e1.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 5px;
  position: relative;
  box-shadow: 0 1px 0px rgba(0, 0, 0, 0.03), 0 1px 1px rgba(0, 0, 0, 0.05); }
  .carousel .left-button {
    position: absolute;
    left: 0;
    top: 190px;
    width: 65px;
    height: 75px;
    background-color: #ebeced;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    background-image: url(/marketinigWepApp/static/media/left-light.1b991c13.svg);
    background-repeat: no-repeat;
    background-position: center; }
  .carousel .right-button {
    position: absolute;
    right: 0;
    top: 190px;
    width: 65px;
    height: 75px;
    background-color: #ebeced;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    background-image: url(/marketinigWepApp/static/media/right-light.f009f6f0.svg);
    background-repeat: no-repeat;
    background-position: center; }

@media (prefers-color-scheme: dark) {
  .carousel .left-button {
    background-color: #323639;
    background-image: url(/marketinigWepApp/static/media/left-dark.a6136ec5.svg); }
  .carousel .right-button {
    background-color: #323639;
    background-image: url(/marketinigWepApp/static/media/right-dark.19495472.svg); } }

@media (prefers-color-scheme: light) {
  .carousel .left-button {
    background-color: #ebeced;
    background-image: url(/marketinigWepApp/static/media/left-light.1b991c13.svg); }
  .carousel .right-button {
    background-color: #ebeced;
    background-image: url(/marketinigWepApp/static/media/right-light.f009f6f0.svg); } }

* {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  list-style: none;
  quotes: none;
  font-style: normal;
  outline: none;
  box-sizing: border-box; }

*:focus {
  outline: none !important; }

input:focus, textarea:focus, select:focus {
  outline: none; }

html, body, #shell {
  background: #ebeced;
  color: #303742;
  overflow-y: scroll;
  cursor: default;
  -webkit-font-smoothing: antialiased;
  font-size: 14px;
  font-family: "Hanken Grotesk", "Helvetica Neue", sans-serif;
  display: block;
  height: 100%; }

@media (prefers-color-scheme: dark) {
  html, body, #shell {
    background: #323639;
    color: white; } }

@media (prefers-color-scheme: light) {
  html, body, #shell {
    background: #ebeced;
    color: #303742; } }

